//  URL
const BASE_URL = 'https://serverzhu.com:3000';

// 
const request = (url, method = 'GET', data = {}) => {
    let token = uni.getStorageSync('token'); //  token

    //  token 
    if (token && isTokenExpired(token)) {
        uni.removeStorageSync('token'); //  token
        redirectToLogin();
        return Promise.reject({ message: "Token " });
    }

    const headers = {
        'Authorization': token ? `Bearer ${token}` : '', //  Bearer 
        'Content-Type': 'application/json',
    };

    return new Promise((resolve, reject) => {
        uni.request({
            url: BASE_URL + url,
            method: method,
            header: headers,
            data: data,
            success: (res) => {
                if (res.statusCode === 200) {
                    resolve(res.data);
                } else if (res.statusCode === 401) {
                    uni.removeStorageSync('token');
                    redirectToLogin();
                    reject({ message: "Token " });
                } else {
                    reject(res.data);
                }
            },
            fail: (err) => {
                reject(err);
            },
        });
    });
};

//  atob  (Polyfill)
function myAtob(input) {
  //  padding
  let str = input.replace(/=+$/, '');
  let output = '';

  const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=';

  for (
    let bc = 0, bs = 0, buffer, i = 0;
    (buffer = str.charAt(i++));
    ~buffer && (bs = bc % 4 ? bs * 64 + buffer : buffer, bc++ % 4)
      ? output += String.fromCharCode(255 & bs >> (-2 * bc & 6))
      : 0
  ) {
    buffer = chars.indexOf(buffer);
  }
  return output;
}

const isTokenExpired = (token) => {
  try {
    // JWT  payload
    const parts = token.split('.');
    if (parts.length !== 3) throw new Error(' Token ');

    const base64 = parts[1].replace(/-/g, '+').replace(/_/g, '/');
    // myAtob
    const decodeStr = myAtob(base64);

    const jsonPayload = JSON.parse(decodeStr);

    const { exp } = jsonPayload;
    return exp * 1000 < Date.now();
  } catch (e) {
    console.error('Token :', e);
    return true;
  }
};


// Token 
const redirectToLogin = () => {
    uni.showToast({ title: '', icon: 'none' });
    setTimeout(() => {
        uni.reLaunch({ url: '/pages/loginPage/loginPage' });
    }, 1000);
};


